import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FingerPrintCaptureService } from './../../../shared/services/finger-print-capture.service';
import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { MatDialogModule, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, FormGroupDirective } from '@angular/forms';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { AccountService } from 'app/views/home/account.service';

@Component({
  selector: 'app-fingerprint',
  templateUrl: './fingerprint.component.html',
  styleUrls: ['./fingerprint.component.scss']
})
export class FingerprintComponent implements OnInit {
  formDirective: FormGroupDirective;
  custFormGroup: FormGroup;
  titleMessage: string = "Please use your fingerprint to authenticate";
  contentMessage: string = "Please place your thumb on the fingerprint reader to proceed.";
  customerId: any;
  tempData: any;
  matchData: any;
  totalItems: number = 0;
  totalPages: number = 0;
  flag: boolean = false;
  constructor(
    private dialogRef: MatDialogRef<FingerprintComponent>,
    private fpCaptureService: FingerPrintCaptureService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar,
    private formBuilder: FormBuilder,

    private ls: LocalStoreService,
    private router: Router,
    private jwtService: JwtAuthService,
    private accountService: AccountService

  ) { }

  ngOnInit() {

    this.custFormGroup = this.formBuilder.group({
      "anyCustomerId": [''],
    })
  }

  applyFilter(event) {
    event.target.value = event.target.value.trim();
    this.customerId = event.target.value;
    console.log("Value : ", this.customerId)

  }

  getCutomerData(formData) {
    console.log('formData :: ', formData);
  }

  goToClose() {
    this.dialogRef.close();
  }

  /* @method : to capture the the finger print */
  captureFingerPrintHttps() {
    console.log("in component");
    this.fpCaptureService.CallingSGIFPCapture()
      .subscribe(capturedData => {
        console.log('capturedData:: ', capturedData);
        if (capturedData.ErrorCode == 0) {
          this.snack.open(`Finding ,is registed customer`, 'OK', {
            duration: 1000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });

          this.getFpDataForMatchOnPageLoad(capturedData.TemplateBase64);
        } else {
          this.getError(capturedData.ErrorCode);
        }
      }, error => {
        this.snack.open(`${error}`, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
  }

  getFpDataForMatchOnPageLoad(templateBase64) {
    console.log('TemplateBase64 :: ', templateBase64);
    this.fpCaptureService.getCustInfoByFp(0, 100, 4)
      .subscribe(resp => {
        console.log('resp:: ', resp);
        // this.totalItems=resp.totalItems;
        // this.totalPages=resp.totalPages;
        if ((resp.totalPages - 1) == 0) {
          Object.keys(resp.data).forEach(key => {
            this.matchFpFromUi(templateBase64, resp.data[key])
          });
        } else {
          for (let i = 0; i < resp.totalPages; i++) {
            if (i == 0) {
              Object.keys(resp.data).forEach(key => {
                this.matchFpFromUi(templateBase64, resp.data[key])
              });
            } else {
              // to-da: need to implement logic when more data is present
            }
          }
        }

      });
  }

  matchFpFromUi(template1, data) {
    this.fpCaptureService.CallingSGIFPMatch(template1, data.fpTemplateBase64)
      .subscribe(fpResp => {
        console.log('fpResp : ', fpResp);
        if (fpResp.MatchingScore >= 100) {
          let custId = data.customerId;
          this.router.navigateByUrl('/others/Service');
          this.ls.setItem('customer_id', custId);
          this.cdr.markForCheck();
          this.cdr.detectChanges();
          this.dialogRef.close({
            message: 'Confirm',
            custId
          });
          this.snack.open(`User found : ${data.customerId}`, 'OK', {
            duration: 1000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          return;
        }
      })
  }

  /* @method : to get error code of finger print device */
  getError(errorCode) {
    let error = this.fpCaptureService.errorCodeService(errorCode);
    console.log('error code', error)
  }

  /* @method : to verify the captured finger print from stored data */
  matchFingerPrint(capturedData) {
    this.fpCaptureService.matchCapturedFp(4, capturedData)
      .subscribe(matchFp => {
        console.log('matchFp:: ', matchFp);
        if (matchFp.customerId) {
          let data = matchFp.customerId;
          this.router.navigateByUrl('/others/Service');
          this.ls.setItem('customer_id', matchFp.customerId);
          this.cdr.markForCheck();
          this.cdr.detectChanges();
          this.dialogRef.close({
            message: 'Confirm',
            data
          });
        } else {
          console.log('else');
        }
      })
  }


  saveBiometric(capFingerPrint, fingerName, cId) {
    let screen = 'Kisok Authenticate';
    this.fpCaptureService.saveBiometric(capFingerPrint, fingerName, cId, screen)
      .subscribe(data => {
        console.log("after save ", data);
        this.cdr.markForCheck();
        this.snack.open('Captured ' + fingerName + ' Finger', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }


}

